import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PscomplexVisaApplicantDetailsComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-visa-applicant-details/ps-complex-visa-applicant-details.component';
import { IOptionsPsInputCountExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-count/ps-input-count.component.interfaces';
import { IOptionsPsDropdownCurrenciesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-currencies/ps-dropdown-currencies.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { ILoginResponse } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsPsFileUploadComponent, IOptionsPsInputAmount, IOptionsPsSelectDropdown, IOptionsPsSelectToggle, IOptionsTemplateMultiOutlets } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
    selector: 'visa-request',
    templateUrl: './visa-request.page.html',
    styleUrls: ['./visa-request.page.scss'],
})
export class VisaRequestPage extends OmniBasePage implements OnInit {
    public visaRequestVO: any = {};
    public formGroup = new FormGroup({});
    public options: IOptionsTemplateMultiOutlets = {
        outletName: 'multi-visa-application',
        requestObject: this.visaRequestVO,
        type: ConstantCommon.OUTLET_TYPE_COMPONENT,
        segmentName: 'visa-',
        component: PscomplexVisaApplicantDetailsComponent,
        group: this.formGroup,
        operID: CommonBussinessConstant.MULTI_VISA_REQUEST_OPER_ID
    };
    @ViewChild('multiTemplateRef') private multiTemplate!: any;

    panelOptions1: IOptionsPsContainerPanel = {
        isExpandable: true,
        labelKey: 'visa_application_details_key',
        iconName: 'document',
        expanded: true
    };

    applicationType: IOptionsPsSelectDropdown = {
        labelKey: 'application_type_key',
        fcName: 'applicationType',
        group: this.formGroup,
        placeHolder: 'please_select_application_type_key',
        selectDefaultItem: { itemValue: 'INDIVIDUAL', description: 'individual_key', selectedObj: { itemValue: 'INDIVIDUAL', description: 'individual_key' } },
        listOfOptions: [{ itemValue: 'INDIVIDUAL', description: 'individual_key', selectedObj: { itemValue: 'INDIVIDUAL', description: 'individual_key' } }, { itemValue: 'GROUP', description: 'group_key', selectedObj: { itemValue: 'GROUP', description: 'group_key' } }]
    };

    transactionAmount: IOptionsPsInputAmount = {
        labelKey: 'transaction_amount_key',
        placeHolder: 'transaction_amount_key',
        fcName: 'transactionAmount',
        group: this.formGroup,
        type: 'amount',
        decimalPoints: 3
    };

    currencyOptions: IOptionsPsDropdownCurrenciesExposed = {
        fcName: 'currencyType',
        group: this.formGroup,
        labelKey: 'transaction_currency_key',
        placeHolder: 'transaction_currency_key',
        iconOptions: {
            iconName: 'filter-report-icon'
        }
    };

    transactionReceiptOptions: IOptionsPsFileUploadComponent = {
        multiple: false,
        fcName: 'transactionReceipt',
        group: this.formGroup,
        labelKey: 'transaction_reciept_key',
        uploadIconName: 'attach'
    };

    applicantIsMe: IOptionsPsSelectToggle = {
        fcName: 'applicant_is_me',
        group: this.formGroup,
        labelKey: 'applicant_is_me_key',
    };

    numberOfApplicants: IOptionsPsInputCountExposed = {
        labelKey: 'number_of_applicants_key',
        fcName: 'numberOfApplicants',
        group: this.formGroup
    };

    cifInfo: any;
    appType = true;
    totalApplicants = 1;
    userInformation: ILoginResponse = {};

    constructor(public commonService: PsCommonService, public logger: LoggerService) {
        super();
    }

    onToggleChange(event) {
        if (event.newValue) {
            this.loadUserInformation();
        } else if (!event.newValue && event.oldValue) {
            this.removeUserInformation();
        }
    }

    onApplicationTypeChange(event) {
        if (event.itemValue === 'GROUP') {
            this.appType = false;
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.applicantIsMe.fcName], 0);
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.numberOfApplicants.fcName], 1);
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.applicantIsMe.fcName], 0);
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.numberOfApplicants.fcName], 1);
            if (JSON.stringify(this.userInformation) != '{}') {
                this.removeUserInformation();
            }
        } else {
            this.appType = true;
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.applicantIsMe.fcName], 1);
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.numberOfApplicants.fcName], 0);
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.applicantIsMe.fcName], 1);
            this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.numberOfApplicants.fcName], 0);
        }
        this.options.appType = event.itemValue;
    }

    numberOfApplicantsChange(event) {
        if (event === 'unlimited') {
            event = 1;
        }
        if (this.totalApplicants < event) {
            this.totalApplicants = event;
            this.multiTemplate.addNewOutlet();
        }
        if (this.totalApplicants > event) {
            this.totalApplicants = event;
            this.multiTemplate.removePanel();
        }
    }

    ngOnInit() {
        super.init();
        this.baseFormGroup = this.formGroup;
    }

    defaultUserInfoKeyMapData = [
        { 'key': 'nationality', 'value': null },
        { 'key': 'firstName', 'value': null },
        { 'key': 'lastName', 'value': null },
        { 'key': 'occupation', 'value': null },
        { 'key': 'email', 'value': null },
        { 'key': 'phone', 'value': null }
    ];

    loadUserInformation() {
        this.userInformation = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
        let filledUserData = [
            { 'key': 'nationality', 'value': this.userInformation.customerInfoCO.countryCode },
            { 'key': 'firstName', 'value': this.userInformation.customerInfoCO.shortName },
            { 'key': 'lastName', 'value': this.userInformation.customerInfoCO.longName },
            { 'key': 'occupation', 'value': this.userInformation.customerInfoCO.occupation },
            { 'key': 'email', 'value': this.userInformation.customerInfoCO.coreEmail },
            { 'key': 'phone', 'value': this.userInformation.customerInfoCO.userMobileNumber }
        ];
        this.multiTemplate.setInfoToVO('visa-1', filledUserData);
    }

    removeUserInformation() {
        this.multiTemplate.setInfoToVO('visa-1', this.defaultUserInfoKeyMapData);
    }

    ionViewDidEnter() {
        super.ionViewDidEnter();
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_DEFAULT_VALUE, [this.applicantIsMe.fcName], false);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_DEFAULT_VALUE, [this.applicationType.fcName], 'INDIVIDUAL');
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_DEFAULT_VALUE, ['visaCountry'], '784');
    }

}
